import React, { Component } from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';
import './font-awesome-4.7.0/css/font-awesome.min.css';

export default class Break extends Component {
	render() {
		return (
			<div className='me-5'>
				<label className='text-primary' id='break-label'>
					Break Length
				</label>
				<div className='control d-flex align-items-center justify-content-center'>
					<i
						id='break-increment'
						className='fa fa-arrow-circle-up fa-2x m-2 text-success'
						aria-hidden='true'
						onClick={this.props.incrementValue}></i>
					<label id='break-length' className='h3 mt-2'>
						{this.props.breakValue}
					</label>
					<i
						id='break-decrement'
						className='fa fa-arrow-circle-down fa-2x m-2 text-danger'
						aria-hidden='true'
						onClick={this.props.decrementValue}></i>
				</div>
			</div>
		);
	}
}
