import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';

export default class Session extends Component {
	render() {
		return (
			<div>
				<label className='text-primary' id='session-label'>
					Session Length
				</label>
				<div className='control d-flex align-items-center justify-content-center'>
					<i
						id='session-increment'
						className='fa fa-arrow-circle-up fa-2x m-2 text-success'
						aria-hidden='true'
						onClick={this.props.incrementValue}></i>
					<label id='session-length' className='h3 mt-2'>
						{this.props.sessionValue}
					</label>
					<i
						id='session-decrement'
						className='fa fa-arrow-circle-down fa-2x m-2 text-danger'
						aria-hidden='true'
						onClick={this.props.decrementValue}></i>
				</div>
			</div>
		);
	}
}
