import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Break from './Break';
import Session from './Session';
import Display from './Display';
import PausePlayReset from './PausePlayReset';

import './Clock.css';

export default class Clock extends Component {
	constructor(props) {
		super(props);
		this.state = {
			currentLabel: 'Session',
			breakValue: 5,
			sessionValue: 25,
			minutes: 25,
			seconds: 0,
			pausePlayClicked: false,
			resetClicked: true,
			breakTriggered: false,
		};
		this.breakIncrement = this.breakIncrement.bind(this);
		this.sessionIncrement = this.sessionIncrement.bind(this);
		this.breakDecrement = this.breakDecrement.bind(this);
		this.sessionDecrement = this.sessionDecrement.bind(this);
		this.resetClicked = this.resetClicked.bind(this);
		this.pausePlayClicked = this.pausePlayClicked.bind(this);
	}

	playAudio() {
		console.log('inside play audio func');
		let audio = document.getElementById('beep');
		audio.play();
	}

	breakIncrement() {
		if (this.state.breakValue < 60 && this.state.breakValue > 0) {
			this.setState({
				breakValue: this.state.breakValue + 1,
			});
		}
	}

	sessionIncrement() {
		if (this.state.sessionValue < 60 && this.state.sessionValue > 0) {
			this.setState({
				sessionValue: this.state.sessionValue + 1,
				minutes: this.state.minutes + 1,
			});
		}
	}

	breakDecrement() {
		if (this.state.breakValue < 60 && this.state.breakValue > 1) {
			this.setState({
				breakValue: this.state.breakValue - 1,
			});
		}
	}

	sessionDecrement() {
		if (this.state.sessionValue < 60 && this.state.sessionValue > 1) {
			this.setState({
				sessionValue: this.state.sessionValue - 1,
				minutes: this.state.minutes - 1,
			});
		}
	}

	pausePlayClicked() {
		clearInterval(this.timer);
		this.setState({
			pausePlayClicked: !this.state.pausePlayClicked,
			resetClicked: false,
		});
		this.timer = setInterval(() => {
			if (this.state.pausePlayClicked) {
				let localMinutes = this.state.minutes;
				let localSeconds = this.state.seconds;
				if (localMinutes > 0) {
					console.log('line 72');
					if (localSeconds > 0) {
						console.log('line 74');
						localSeconds = localSeconds - 1;
					} else {
						console.log('line 79');
						localMinutes = localMinutes - 1;
						localSeconds = 59;
					}
				} else {
					if (localSeconds > 0) {
						localSeconds = localSeconds - 1;
					} else {
						this.playAudio();
						//All Break and Session logic here.
						if (this.state.breakTriggered === false) {
							localMinutes = this.state.breakValue;
							this.setState({
								breakTriggered: !this.state.breakTriggered,
								currentLabel: 'Break',
							});
						} else {
							localMinutes = this.state.sessionValue;
							this.setState({
								breakTriggered: !this.state.breakTriggered,
								currentLabel: 'Session',
							});
						}
					}
				}

				this.setState({
					minutes: localMinutes,
					seconds: localSeconds,
				});
			}
		}, 300);
	}

	resetClicked() {
		clearInterval(this.timer);
		let audio = document.getElementById('beep');
		audio.pause();
		audio.currentTime = 0;
		this.setState({
			currentLabel: 'Session',
			breakValue: 5,
			sessionValue: 25,
			minutes: 25,
			seconds: 0,
			pausePlayClicked: false,
			resetClicked: true,
			breakTriggered: false,
		});
	}

	render() {
		const paddedMinutes = String(this.state.minutes).padStart(2, '0');
		const paddedSeconds = String(this.state.seconds).padStart(2, '0');
		return (
			<div className='Clock bg-warning text-white d-flex flex-column justify-content-center'>
				<label className='AppTitle h3 bg-white text-primary'>
					25 + 5 Clock
				</label>
				<div className='Controls d-flex justify-content-center p-3'>
					<Break
						breakValue={this.state.breakValue}
						incrementValue={this.breakIncrement}
						decrementValue={this.breakDecrement}
					/>
					<Session
						sessionValue={this.state.sessionValue}
						incrementValue={this.sessionIncrement}
						decrementValue={this.sessionDecrement}
					/>
				</div>
				<div className='ClockDisplay'>
					<Display
						clockText={this.state.currentLabel}
						timeLeft={paddedMinutes.concat(':').concat(paddedSeconds)}
						pausePlayClicked={this.state.pausePlayClicked}
					/>
				</div>
				<PausePlayReset
					pausePlayClicked={this.pausePlayClicked}
					resetClicked={this.resetClicked}
				/>
				<audio
					id='beep'
					preload='auto'
					src='https://raw.githubusercontent.com/freeCodeCamp/cdn/master/build/testable-projects-fcc/audio/BeepSound.wav'></audio>
			</div>
		);
	}
}
